.top--header {
  .top--header_bg {
    background-image: url(../../img/Bg-Dark2.svg);
    background-size: cover;
    padding: 20px 0;
  }

  .top--header_text {
    @include font(20px, $font-thin, $bg-primary-3, 100);
  }

  .top--header_number {
    @include font(20px, $font-regular, $bg-primary-3, 400);
    text-decoration: none;
  }

  .top--header_buttons {
    text-align: right;
    a {
      text-align: left;
    }
  }
  a:first-child {
    margin-right: 30px;
  }
}
